.select {
  width: 50%;
  margin: 1% auto;
  font-weight: 600;
}

td[Checked] {
  background: rgb(105, 5, 5);
}

td[In] {
  background: rgb(255, 255, 142);
}

td[Checking] {
  background: rgb(255, 159, 183);
}

td[ToDo] {
  background: rgb(170, 165, 165);
}

td[seen] {
  background: rgb(177, 255, 177);
}
