.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 10s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: rgb(160, 158, 158);
  min-height: 1vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

table {
  border: 8px double rgb(160, 158, 158);
  border-collapse: collapse;
  margin: 0 auto;
  font-size: 20px;
  background: #ffffff;
}

th {
  text-align: center;
  padding: 5px;
  border: 2px solid rgb(160, 158, 158);
}

td {
  padding: 5px;
  border: 1px solid rgb(160, 158, 158);
}

main {
  max-width: 100vw;
  padding: 10px;
  background-color: rgb(227, 248, 246);
}
