body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: rgb(160, 158, 158);
  min-height: 1vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

table {
  border: 8px double rgb(160, 158, 158);
  border-collapse: collapse;
  margin: 0 auto;
  font-size: 20px;
  background: #ffffff;
}

th {
  text-align: center;
  padding: 5px;
  border: 2px solid rgb(160, 158, 158);
}

td {
  padding: 5px;
  border: 1px solid rgb(160, 158, 158);
}

main {
  max-width: 100vw;
  padding: 10px;
  background-color: rgb(227, 248, 246);
}

.select {
  width: 50%;
  margin: 1% auto;
  font-weight: 600;
}

td[Checked] {
  background: rgb(105, 5, 5);
}

td[In] {
  background: rgb(255, 255, 142);
}

td[Checking] {
  background: rgb(255, 159, 183);
}

td[ToDo] {
  background: rgb(170, 165, 165);
}

td[seen] {
  background: rgb(177, 255, 177);
}

